<template>
  <div class="result">
    <h1>
      {{ $route.query.title
      }}<span class="count"
        > <span>{{ total }}</span> </span
      >
    </h1>
    <div class="musicBox">
      <!-- // 1 -->
      <el-tabs v-model="activeName" @tab-click="page = 1">
        <!--  -->
        <el-tab-pane label="" name="song">
          <table>
            <thead v-show="total">
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </thead>
            <tbody>
              <tr class="T_row" v-for="(song, index) in songs" :key="index">
                <td>{{ index + 1 }}</td>
                <td>
                  <el-link
                    type="info"
                    @click.native="player(song.id, song.name)"
                    >{{ song.name }}</el-link
                  >
                  <!-- mvmv -->
                  <span
                    v-if="song.mvid != 0"
                    class="pointer mv el-icon-video-camera"
                    @click="toMvList(song.mvid, song.artists[0].id)"
                  ></span>
                </td>
                <!-- @click="play(song.id)" -->
                <td>{{ song.artists[0].name }}</td>
                <td>{{ song.album.name }}</td>
                <td>{{ $store.getters.formatDate(song.duration) }}</td>
              </tr>
            </tbody>
          </table>
        </el-tab-pane>
        <!--  -->
        <el-tab-pane label="" name="songList">
          <div class="song_wrap">
            <!-- id  -->
            <div
              class="song_box pointer"
              v-for="(item, index) in playlists"
              @click="toSongList(item.id)"
              :key="index"
            >
              <div class="song_boxImg pointer">
                <p>:{{ item.playCount }}</p>
                <img :src="item.coverImgUrl" />
              </div>
              <p class="song_boxTitle">{{ item.name }}</p>
            </div>
          </div>
        </el-tab-pane>
        <!-- MV -->
        <el-tab-pane label="MV" name="Mv">
          <div class="mv_wrap">
            <div class="mvBox" v-for="(mv, index) in mvList" :key="index">
              <div class="mvBox_Img">
                <img :src="mv.cover" alt="" />
                <i class="el-icon-caret-right">{{
                  mv.playCount > 100000 ? "10w+" : mv.playCount
                }}</i>
                <!-- mv -->
                <span
                  class="el-icon-video-play iconPlay"
                  @click="toMvList(mv.id, mv.artistId)"
                ></span>
                <span class="time">{{ mv.duration | formatDate }}</span>
              </div>
              <div class="mvBox_Msg">
                <p>{{ mv.name }}</p>
                <p>{{ mv.artistName }}</p>
              </div>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>

      <el-empty v-show="!total" description="" />
      <!--  -->
      <el-pagination
        v-show="total"
        background
        layout="prev, pager, next"
        :current-page="page"
        :total="total"
        @current-change="update"
      />
    </div>
  </div>
</template>

<script>
import { requestWyy } from "../request/app";
export default {
  name: "Result",

  data() {
    return {
      searchVal: this.$route.query.title,
      activeName: "song",
      // 
      type: 1,
      limit: 10,

      // 
      page: 1,
      // 
      total: 0,

      // ----------

      // 
      songs: [],
      // mv
      mvid: 0,

      // ----------

      // --------------
      playlists: [],

      // mv---------
      mvList: [],
    };
  },
  // 
  filters: {
    formatDate(val) {
      const all = val / 1000;
      const m = (parseInt(all / 60) + "").padStart(2, "0");
      const s = (parseInt(all % 60) + "").padStart(2, "0");
      return m + ":" + s;
    },
  },

  watch: {
    //  
    $route: {
      // 
      immediate: true,
      handler() {
        this.searchVal = this.$route.query.title;
        this.page = 1;
        this.search();
      },
    },
    activeName: {
      // type
      handler(val) {
        if (val == "song") {
          this.type = 1;
        } else if (val == "songList") {
          this.type = 1000;
        } else {
          this.type = 1004;
        }
        this.search();
      },
    },
  },
  methods: {
    search() {
      // 
      requestWyy({
        url: "/search",
        params: {
          keywords: this.searchVal,
          limit: this.limit,
          type: this.type,
          offset: (this.page - 1) * 10,
        },
      }).then(({ data: { result } }) => {
        // ----------------------
        if (this.type == 1) {
          // 
          this.songs = result.songs;
          // mv
          this.mvid = result.mvid;
          // 
          this.total = result.songCount;
          // }
        } else if (this.type == 1000) {
          // ---------------
          this.playlists = result.playlists;
          this.total = result.playlistCount;
        } else {
          // mv-------------
          this.mvList = result.mvs;
          this.total = result.mvCount;
        }
      });
    },

    // -------------
    //  
    player(id, name) {
      this.$store.dispatch("aPlay", { id, name });
    },
    // 
    update(cur) {
      this.page = cur;
      this.search();
    },
    // -------------

    // id
    toSongList(id) {
      this.$router.push({
        path: "/songlist",
        query: {
          id,
        },
      });
    },
    // mv id
    toMvList(id, artistId) {
      this.$router.push({
        path: "/mvlist",
        query: {
          id,
          artistId,
        },
      });
    },
  },
};
</script>
   
