<template>
  <div class="detail_container">
    <!--  -->
    <section class="detail_top">
      <!--  -->
      <div class="top_left">
        <img :src="playMsg.coverImgUrl" />
      </div>
      <!--  -->
      <section class="top_right">
        <h2 class="title">{{ playMsg.name }}</h2>
        <!-- , -->
        <p class="itme_msg">
          <img :src="playMsg.creator.avatarUrl" alt="" />
          <span>{{ playMsg.creator.nickname }}</span
          ><span class="small">{{
            $store.getters.formatDay(playMsg.createTime, "xxxx-mm-dd")
          }}</span
          >
        </p>
        <!--  -->
        <div class="tag">
          <span class="small">
            <span v-for="(tag, index) in playMsg.tags" :key="index"
              >{{ tag }}</span
            >
            <!--  -->
            <span v-if="!playMsg.tags.length"></span>
          </span>
        </div>
        <!--  -->
        <p class="content">
          <span class="small">{{ playMsg.description }}</span>
        </p>
      </section>
    </section>
    <el-tabs v-model="activeName" class="detail_bottom">
      <el-tab-pane label="" name="first">
        <!--  -->
        <table>
          <thead>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </thead>
          <tbody>
            <tr class="T_row" v-for="(song, index) in tracks" :key="index">
              <td>{{ index + 1 }}</td>
              <td>
                <div class="songImg">
                  <!--  -->
                  <img :src="song.al.picUrl" /><span
                    class="el-icon-video-play iconPlay"
                    @click="player(song.id, song.name)"
                  ></span>
                </div>
              </td>
              <td>
                {{ song.name }}
                <!-- mvmv -->
                <span
                  v-if="song.mv != 0"
                  class="pointer mv el-icon-video-camera"
                  @click="toMvList(song.mv, song.ar[0].id)"
                ></span>
              </td>
              <td>{{ song.ar[0].name }}</td>
              <td>{{ song.al.name }}</td>
              <td>{{ $store.getters.formatDate(song.dt) }}</td>
            </tr>
          </tbody>
        </table>
      </el-tab-pane>
      <!--  -->
      <el-tab-pane label="" name="fourth">
        <section class="conmentWrap">
          <!--  -->
          <div class="Comment">
            <h4>
              (<span>{{ hotAll }}</span
              >)
            </h4>
            <section
              class="commentBox"
              v-for="(item, index) in hot"
              :key="index"
            >
              <!--  -->
              <div class="img">
                <img :src="item.user.avatarUrl" alt="" />
              </div>
              <!--  -->
              <section class="comments">
                <p class="mainComment">
                  <span class="name">{{ item.user.nickname }}:</span><br />{{
                    item.content
                  }}
                </p>
                <!--  -->
                <p class="reply" v-if="item.beReplied.length">
                  <span class="name"
                    >{{ item.beReplied[0].user.nickname }}:</span
                  >{{ item.beReplied[0].content }}
                </p>
                <p class="small">{{ $store.getters.formatDay(item.time) }}</p>
              </section>
            </section>
            <!-- 10 -->
            <el-pagination
              v-if="hotAll > 10"
              background
              layout="prev, pager, next"
              :total="hotAll"
              :current-page="hotPage"
              @current-change="hotUpdate"
            />
          </div>
          <!--  -->
          <el-divider
            ><i class="el-icon-caret-bottom"></i
            ><i class="el-icon-caret-bottom"></i
            ><i class="el-icon-caret-bottom"></i
            ><i class="el-icon-caret-bottom"></i
          ></el-divider>
          <!--  -->
          <div class="Comment">
            <h4>
              (<span>{{ newAll }}</span
              >)
            </h4>
            <section
              class="commentBox"
              v-for="(item, index) in news"
              :key="index"
            >
              <!--  -->
              <div class="img">
                <img :src="item.user.avatarUrl" />
              </div>
              <!--  -->
              <section class="comments">
                <p class="mainComment">
                  <span class="name">{{ item.user.nickname }}:</span><br />{{
                    item.content
                  }}
                </p>
                <!--   -->
                <p class="reply" v-if="item.beReplied.length">
                  <span class="name"
                    >{{ item.beReplied[0].user.nickname }}:</span
                  >{{ item.beReplied[0].content }}
                </p>
                <p class="small">{{ $store.getters.formatDay(item.time) }}</p>
              </section>
            </section>
            <!-- 10 -->
            <el-pagination
              v-if="newAll > 10"
              background
              layout="prev, pager, next"
              :total="newAll"
              :current-page="newPage"
              @current-change="newUpdate"
            />
          </div>
        </section>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
//  requestWyy
import { requestWyy } from "../request/app";
export default {
  name: "SongList",
  data() {
    return {
      activeName: "first",
      // 
      playMsg: [],
      // 
      tracks: [],

      // hot
      hotAll: 0,
      // hot
      hot: [],
      // 
      hotPage: 1,

      // new
      newAll: 0,
      // hot
      news: [],
      // 
      newPage: 1,
    };
  },
  methods: {
    getMsg() {
      requestWyy({
        url: "/playlist/detail",
        params: {
          id: this.$route.query.id,
        },
      }).then(({ data }) => {
        // console.log(data);
        this.playMsg = data.playlist;
        this.tracks = data.playlist.tracks;
      });
    },
    // 
    getHotComment() {
      // hot
      requestWyy({
        url: "/comment/hot",
        params: {
          id: this.$route.query.id,
          limit: 10,
          type: 2,
          offset: (this.hotPage - 1) * 10,
        },
      }).then(({ data }) => {
        this.hotAll = data.total;
        this.hot = data.hotComments;
      });
    },
    // 
    getNewComment() {
      // new
      requestWyy({
        url: "/comment/playlist",
        params: {
          id: this.$route.query.id,
          limit: 10,
          offset: (this.newPage - 1) * 10,
        },
      }).then(({ data }) => {
        this.newAll = data.total;
        this.news = data.comments;
      });
    },
    // 
    hotUpdate(cur) {
      this.hotPage = cur;
      this.getHotComment();
    },
    // 
    newUpdate(cur) {
      this.newPage = cur;
      this.getNewComment();
    },

    //  
    player(id, name) {
      this.$store.dispatch("aPlay", { id, name });
    },
    // mv id
    toMvList(id, artistId) {
      this.$router.push({
        path: "/mvlist",
        query: {
          id,
          artistId,
        },
      });
    },
  },

  watch: {
    "$route.query.id": {
      // 
      immediate: true,
      handler() {
        this.getMsg();
        this.getHotComment();
        this.getNewComment();
      },
    },
  },
};
</script>

