<template>
  <div class="mvs-container">
    <div class="filter_wrap">
      <div class="place_wrap">
        <span class="wrap_title">: </span>
        <ul class="small">
          <li :class="{ active: area == '' }" @click="area = ''">
            
          </li>
          <li :class="{ active: area == '' }" @click="area = ''">
            
          </li>
          <li :class="{ active: area == '' }" @click="area = ''">
            
          </li>
          <li :class="{ active: area == '' }" @click="area = ''">
            
          </li>
          <li :class="{ active: area == '' }" @click="area = ''">
            
          </li>
          <li :class="{ active: area == '' }" @click="area = ''">
            
          </li>
        </ul>
      </div>
      <div class="type_wrap">
        <span class="wrap_title">:</span>
        <ul class="small">
          <li :class="{ active: type == '' }" @click="type = ''">
            
          </li>
          <li :class="{ active: type == '' }" @click="type = ''">
            
          </li>
          <li :class="{ active: type == '' }" @click="type = ''">
            
          </li>
          <li :class="{ active: type == '' }" @click="type = ''">
            
          </li>
          <li
            :class="{ active: type == '' }"
            @click="type = ''"
          >
            
          </li>
        </ul>
      </div>
      <div class="order_wrap">
        <span class="wrap_title">:</span>
        <ul class="small">
          <li
            :class="{ active: order == '' }"
            @click="order = ''"
          >
            
          </li>
          <li :class="{ active: order == '' }" @click="order = ''">
            
          </li>
          <li :class="{ active: order == '' }" @click="order = ''">
            
          </li>
        </ul>
      </div>
    </div>
    <div class="mv_wrap">
      <div class="mvBox" v-for="(song, index) in songMsg" :key="index">
        <div class="mvBox_Img">
          <img :src="song.cover" alt="" />
          <i class="el-icon-caret-right">{{
            song.playCount > 100000 ? "10w+" : song.playCount
          }}</i>
          <!-- mv -->
          <span
            class="el-icon-video-play iconPlay"
            @click="toMvList(song.id, song.artistId || 854363)"
          ></span>
          <span class="time">{{
            $store.getters.formatDate(song.duration)
          }}</span>
        </div>
        <div class="mvBox_Msg">
          <p>{{ song.name }}</p>
          <p>{{ song.artistName }}</p>
        </div>
      </div>
    </div>
    <!--  -->
    <el-pagination
      background
      layout="prev, pager, next"
      :page-size="8"
      :total="total"
      :current-page="page"
      @current-change="update"
    />
  </div>
</template>

<script>
import { requestWyy } from "../request/app";
export default {
  name: "Mv",

  data() {
    return {
      // 
      songMsg: [],
      // 
      total: 0,
      // 
      page: 1,
      // 
      limit: 8,
      // 
      area: "",
      type: "",
      order: "",
    };
  },
  methods: {
    getSong() {
      requestWyy({
        url: "/mv/all",
        params: {
          area: this.area,
          type: this.type,
          order: this.order,
          limit: this.limit,
          offset: (this.page - 1) * 8,
        },
      }).then(({ data }) => {
        this.songMsg = data.data;
        // mv    count
        if (data.count) {
          this.total = data.count;
        }
      });
    },
    // 
    update(cur) {
      this.page = cur;
    },
    // mv id
    toMvList(id, artistId) {
      this.$router.push({
        path: "/mvlist",
        query: {
          id,
          artistId,
        },
      });
    },
  },
  watch: {
    // 
    page: {
      immediate: true,
      handler() {
        this.getSong();
      },
    },
    //    1
    area() {
      this.page = 1;
      this.getSong();
    },
    type() {
      this.page = 1;
      this.getSong();
    },
    order() {
      this.page = 1;
      this.getSong();
    },
  },
};
</script>

