<template>
  <div class="recommandList">
    <div class="introduce_box">
      <div class="left_msg">
        <img :src="listDescribe.coverImgUrl" alt="" />
        <div class="tilteMsg">
          <span class="pointer"></span>
          <h4>
            {{ listDescribe.copywriter }}
          </h4>
          <p>
            {{ listDescribe.description }}
          </p>
        </div>
      </div>
      <div class="bg_mask">
        <div></div>
        <img :src="listDescribe.coverImgUrl" class="bg_img" alt="" />
      </div>
    </div>
    <div class="listClass">
      <ul class="small">
        <li :class="{ active: tag == '' }" @click="tag = ''"></li>
        <li :class="{ active: tag == '' }" @click="tag = ''"></li>
        <li :class="{ active: tag == '' }" @click="tag = ''"></li>
        <li :class="{ active: tag == '' }" @click="tag = ''"></li>
        <li :class="{ active: tag == '' }" @click="tag = ''"></li>
        <li :class="{ active: tag == '' }" @click="tag = ''"></li>
        <li :class="{ active: tag == '' }" @click="tag = ''"></li>
        <li :class="{ active: tag == '' }" @click="tag = ''"></li>
        <li :class="{ active: tag == '' }" @click="tag = ''">
          
        </li>
        <li :class="{ active: tag == '' }" @click="tag = ''">
          
        </li>
        <li :class="{ active: tag == 'ACG' }" @click="tag = 'ACG'">ACG</li>
        <li :class="{ active: tag == '' }" @click="tag = ''"></li>
      </ul>
    </div>
    <!--  -->
    <div class="song_wrap">
      <div
        class="song_box pointer"
        v-for="(song, index) in songList"
        @click="toSongList(song.id)"
        :key="index"
      >
        <div class="song_boxImg">
          <p>:{{ song.playCount }}</p>
          <img :src="song.coverImgUrl" alt="" />
        </div>
        <p class="song_boxTitle">{{ song.name }}</p>
      </div>
    </div>
    <el-pagination
      background
      layout="prev, pager, next"
      :total="pages"
      @current-change="update"
      :current-page="this.curPage"
    >
    </el-pagination>
  </div>
</template>

<script>
import { requestWyy } from "../request/app";
export default {
  name: "Recommend",

  data() {
    return {
      // 
      tag: "",
      // 
      listDescribe: [],

      // 
      songList: [],
      // 
      pages: 1,
      // 
      curPage: 1,
    };
  },

  // 
  watch: {
    tag: {
      // 
      immediate: true,
      handler() {
        // 
        this.topData();

        // 
        this.curPage = 1;

        // 
        this.listData();
      },
    },
  },
  methods: {
    topData() {
      // 
      requestWyy({
        url: "/top/playlist/highquality",
        params: {
          limit: 1,
          cat: this.tag,
        },
      }).then((res) => {
        this.listDescribe = res.data.playlists[0];
      }),
        (error) => {
          this.$message.error("~");
        };
    },
    listData() {
      // 
      requestWyy({
        url: "/top/playlist/",
        params: {
          limit: 10,
          offset: (this.curPage - 1) * 10,
          cat: this.tag,
        },
      }).then((res) => {
        this.songList = res.data.playlists;
        // 
        this.pages = res.data.total;
      }),
        (error) => {
          this.$message.error("~");
        };
    },

    // 
    update(cur) {
      this.curPage = cur;
      this.listData();
    },

    // id
    toSongList(id) {
      this.$router.push({
        path: "/songlist",
        query: {
          id,
        },
      });
    },
  },
};
</script>

