<template>
  <div class="song_container">
    <div class="listClass">
      <ul class="small">
        <li
          :class="{ active: tag == '' }"
          @click="
            tag = '';
            typeId = 0;
          "
        >
          
        </li>
        <li
          :class="{ active: tag == '' }"
          @click="
            tag = '';
            typeId = 96;
          "
        >
          
        </li>
        <li
          :class="{ active: tag == '' }"
          @click="
            tag = '';
            typeId = 7;
          "
        >
          
        </li>
        <li
          :class="{ active: tag == '' }"
          @click="
            tag = '';
            typeId = 8;
          "
        >
          
        </li>
        <li
          :class="{ active: tag == '' }"
          @click="
            tag = '';
            typeId = 16;
          "
        >
          
        </li>
      </ul>
    </div>
    <table v-if="!isLoading">
      <thead>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </thead>
      <tbody>
        <tr class="T_row" v-for="(song, index) in songMsg" :key="index">
          <td>{{ index + 1 }}</td>
          <td>
            <div class="songImg">
              <img :src="song.album.picUrl" /><span
                class="el-icon-video-play iconPlay"
                @click="player(song.id, song.name)"
              ></span>
            </div>
          </td>
          <td>
            {{ song.name }}
            <!-- mvmv -->
            <span
              v-if="song.mvid != 0"
              class="pointer mv el-icon-video-camera"
              @click="toMvList(song.mvid, song.artists[0].id)"
            ></span>
          </td>
          <td>{{ song.artists[0].name }}</td>
          <td>{{ song.album.name }}</td>
          <td>{{ $store.getters.formatDate(song.duration) }}</td>
        </tr>
      </tbody>
    </table>
    <!--  -->
    <div v-else class="load el-icon-loading"></div>
  </div>
</template>

<script>
import { requestWyy } from "../request/app";
export default {
  name: "Songs",

  data() {
    return {
      // 
      tag: "",
      // 
      isLoading: true,
      // 
      typeId: 0,
      //
      songMsg: [],
    };
  },
  watch: {
    tag: {
      immediate: true,
      handler() {
        // 
        this.isLoading = true;
        // 
        this.getSong();
      },
    },
  },
  methods: {
    getSong() {
      requestWyy({
        url: "/top/song",
        params: {
          type: this.typeId,
        },
      }).then(({ data: { data } }) => {
        //     20
        this.songMsg = data.slice(0, 20);
        this.isLoading = false;
      });
    },
    //  
    player(id, name) {
      this.$store.dispatch("aPlay", { id, name });
    },
    // mv id
    toMvList(id, artistId) {
      this.$router.push({
        path: "/mvlist",
        query: {
          id,
          artistId,
        },
      });
    },
  },
};
</script>

