<template>
  <div>
    <el-form
      ref="loginForm"
      :model="loginForm"
      class="loginContainer"
      :rules="rules"
    >
      <h3 class="loginTitle"></h3>
      <!--  -->
      <el-form-item prop="username">
        <el-input
          type="text"
          v-model="loginForm.username"
          placeholder=""
        />
      </el-form-item>
      <el-form-item prop="password">
        <el-input
          type="password"
          v-model="loginForm.password"
          placeholder=""
        />
      </el-form-item>
      <el-form-item>
        <el-checkbox v-model="checked" class="loginRemember"
          ></el-checkbox
        >
        <el-button type="primary" style="width: 100%" @click="submitLogin"
          ></el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { requestWyy } from "../request/app";
export default {
  name: "Login",
  data() {
    return {
      loginForm: {
        username: localStorage.getItem("phone"),
        password: localStorage.getItem("pwd"),
      },
      //   
      rules: {
        username: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          {
            type: "string",
            message: "",
            len: 11,
            trigger: "blur",
          },
        ],
        password: [{ required: true, message: "", trigger: "blur" }],
      },
      // 
      checked: true,
    };
  },
  methods: {
    submitLogin() {
      // 
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          // 
          requestWyy({
            url: "/login/cellphone",
            params: {
              phone: this.loginForm.username,
              password: this.loginForm.password,
            },
          })
            .then(({ data }) => {
              if (data.code == 200) {
                this.$message.success("");
                // true
                this.$store.commit("logIn");
                // token
                this.$store.state.login.token = data.token;
                sessionStorage.setItem("token", this.$store.state.login.token);
                // userInfosession;
                this.$store.state.login.userInfo = data.profile;
                sessionStorage.setItem("userInfo", [
                  JSON.stringify(data.profile),
                ]);
                // 
                this.$router.replace("/myhome");
                console.log(data.profile);
                // 
                if (this.checked) {
                  localStorage.setItem("phone", this.loginForm.username);
                  localStorage.setItem("pwd", this.loginForm.password);
                } else {
                  localStorage.removeItem("phone");
                  localStorage.removeItem("pwd");
                }
              } else if (data.code == 502) {
                this.$message.error(data.msg);
                console.log(this);
              } else {
                this.$message.error("");
              }
            })
            .catch(function (error) {
              // console.log(error.message);
              // alert("");
            });
        } else {
          this.$message.error("");
          return false;
        }
      });
    },
  },
};
</script>

<style lang="less" scoped>
.loginContainer {
  width: 400px;
  margin: 80px auto;
  padding: 15px 35px 15px 35px;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ccc;
  border-radius: 15px;
  box-shadow: 0 0 25px #ccc;
  .loginTitle {
    margin: 0 auto 45px auto;
    text-align: center;
  }
  .loginRemember {
    text-align: left;
    margin: 0 0 10px 0;
  }
}
</style>