<template>
  <div class="mv_container">
    <section class="mv_top">
      <!-- mv -->
      <div class="mv_box">
        <!-- mv -->
        <div class="mv_play">
          <!-- mv -->
          <h2 class="mvName"><span class="mvIcon">MV</span>{{ mvMsg.name }}</h2>
          <video
            controls
            ref="video"
            :src="mvUrl"
            :poster="mvMsg.cover"
            @play="play"
            autoplay
          ></video>
        </div>
        <!-- mv -->
        <div class="mv_detail">
          <!--  -->
          <p class="author">
            <img :src="autorMsg.picUrl" alt="" /><span class="authorName">{{
              mvMsg.artistName
            }}</span>
          </p>
          <!--  -->
          <span class="small">{{ mvMsg.publishTime }}</span>
          <!--  -->
          <p class="small">{{ autorMsg.briefDesc || "" }}</p>
        </div>
      </div>
      <!-- MV -->
      <div class="mv_recommend">
        <h3></h3>
        <div class="mv_wrap">
          <div class="mvBox" v-for="(mv, index) in mvList" :key="index">
            <div class="mvBox_Img">
              <img :src="mv.cover" />
              <i class="el-icon-caret-right">{{
                mv.playCount > 100000 ? "10w+" : mv.playCount
              }}</i>
              <span
                class="el-icon-video-play iconPlay"
                @click="toMvList(mv.id, mv.artistId)"
              ></span>
              <span class="time">{{
                $store.getters.formatDate(mv.duration)
              }}</span>
            </div>
            <div class="mvBox_Msg">
              <p>{{ mv.name }}</p>
              <p>{{ mv.artistName }}</p>
            </div>
          </div>
        </div>
      </div>
    </section>
    <!-- mv -->
    <section class="conmentWrap">
      <!--  -->
      <div class="Comment">
        <h4>
          (<span>{{ hotAll }}</span
          >)
        </h4>
        <section class="commentBox" v-for="(item, index) in hot" :key="index">
          <!--  -->
          <div class="img">
            <img :src="item.user.avatarUrl" alt="" />
          </div>
          <!--  -->
          <section class="comments">
            <p class="mainComment">
              <span class="name">{{ item.user.nickname }}:</span><br />{{
                item.content
              }}
            </p>
            <!--  -->
            <p class="reply" v-if="item.beReplied.length">
              <span class="name">{{ item.beReplied[0].user.nickname }}: </span
              >{{ item.beReplied[0].content }}
            </p>
            <p class="small">{{ $store.getters.formatDay(item.time) }}</p>
          </section>
        </section>
      </div>
      <!--  -->
      <div class="Comment">
        <h4>
          (<span>{{ newAll }}</span
          >)
        </h4>
        <section class="commentBox" v-for="(item, index) in news" :key="index">
          <!--  -->
          <div class="img">
            <img :src="item.user.avatarUrl" />
          </div>
          <!--  -->
          <section class="comments">
            <p class="mainComment">
              <span class="name">{{ item.user.nickname }}:</span><br />{{
                item.content
              }}
            </p>
            <!--   -->
            <p class="reply" v-if="item.beReplied.length">
              <span class="name">{{ item.beReplied[0].user.nickname }}:</span
              >{{ item.beReplied[0].content }}
            </p>
            <p class="small">{{ $store.getters.formatDay(item.time) }}</p>
          </section>
        </section>
        <!-- 10 -->
        <el-pagination
          v-if="newAll > 10"
          background
          layout="prev, pager, next"
          :total="newAll"
          :current-page="page"
          @current-change="update"
        />
      </div>
    </section>
  </div>
</template>

<script>
import { requestWyy } from "../request/app";
export default {
  name: "MvList",
  data() {
    return {
      // mv
      mvUrl: "",
      //   mv
      mvMsg: [],
      //   
      autorMsg: [],
      //   mv
      mvList: [],

      // 
      page: 1,

      // hot
      hotAll: 0,
      // hot
      hot: [],
      // new
      newAll: 0,
      // hot
      news: [],
    };
  },
  methods: {
    //   mvreplace
    // mv id
    toMvList(id, artistId) {
      this.$router.replace({
        path: "/mvlist",
        query: {
          id,
          artistId,
        },
      });
    },
    //   mv
    getMvList() {
      requestWyy({
        url: "/simi/mv",
        params: {
          mvid: this.$route.query.id,
        },
      }).then(({ data: { mvs } }) => {
        this.mvList = mvs;
      });
    },
    //   mv
    getMv() {
      requestWyy({
        url: "/mv/url",
        params: {
          //   MVid
          id: this.$route.query.id,
        },
      }).then(({ data: { data } }) => {
        this.mvUrl = data.url;
      });
    },
    // mv
    getMvMsg() {
      requestWyy({
        url: "/mv/detail",
        params: {
          mvid: this.$route.query.id,
        },
      }).then(({ data: { data } }) => {
        this.mvMsg = data;
      });
    },
    // 
    getManMsg() {
      requestWyy({
        url: "/artists",
        params: {
          //   id
          id: this.$route.query.artistId,
        },
      }).then(({ data }) => {
        this.autorMsg = data.artist;
      });
    },
    // mv
    getCommpent() {
      requestWyy({
        url: "/comment/mv",
        params: {
          //   MVid
          id: this.$route.query.id,
          limit: 10,
          offset: (this.page - 1) * 10,
        },
      }).then(({ data }) => {
        //   
        this.newAll = data.total;
        // z
        this.news = data.comments;
        // hot
        this.hotAll = data.hotComments.length;
        // hot
        this.hot = data.hotComments;
      });
    },
    // 
    update(cru) {
      this.page = cru;
      this.getCommpent();
    },
    play() {
      this.$store.commit("playVideo");
    },
  },
  // video 
  mounted() {
    this.$store.state.ref.video = this.$refs.video;
    console.log();
  },
  watch: {
    //   mv 
    "$route.query.id": {
      // create
      immediate: true,
      handler() {
        this.getMv();
        this.getMvMsg();
        this.getManMsg();
        this.getMvList();
        this.getCommpent();
      },
    },
  },
};
</script>

